import axios from 'axios';

import {token, uri} from '../../../api/config';

export const api = Object.freeze({
    getActors: {
        fetch: (tag,id) => {
            return axios.get(`${uri}/${tag}/${id}/credits`, {
                params: {
                    api_key: token,
                    language: 'ru-RU'
                }
            })
        }
    },
});
