// Core
import React, { useState } from 'react';
// Styles
import Styles from './styles/index.module.scss';
// Elements
import { NavLink } from 'react-router-dom';

import CloseIcon from '@material-ui/icons/Close';
import MenuIcon from '@material-ui/icons/Menu';
import { book } from '../../navigation/book';
import cx from 'classnames';

export const Sider = () => {
  const routesJSX = Object.values(book).map(({url, title, menu}) => (
    menu && <NavLink to={url} exact={true} key={url} className={Styles.sider__item} activeClassName={Styles.isActive}>
      <span className='nav-text'>{title}</span>
    </NavLink>
  ));
  const [stateMenu, setStateMenu] = useState(false);

  const toggleMenuActive = () => {
    setStateMenu(!stateMenu);
  };

  const menuBtn = cx({
    [Styles.sider__menuBtn]: true,
    [Styles.isActive]: stateMenu,
  });

  const sider = cx({
    [Styles.sider]: true,
    [Styles.isActive]: stateMenu,
  });
    return (
      <>
        <div className={menuBtn} onClick={toggleMenuActive}>
          { stateMenu ? <CloseIcon /> : <MenuIcon /> }
        </div>
        <aside className={sider}>
            {routesJSX}
        </aside>
        </>
    )
};
