// Core
import React from 'react';
// Styles
import Styles from './styles/index.module.scss';
import { imagesSource } from '../../api/config';
// Elements
import { goToPage } from '../../helpers/goToPage';

export const FilmCard = ({ poster_path, title, name, id, tag, release_date, vote_average }) => {
    const year = release_date && new Date(release_date).getFullYear();
    return poster_path && (
      <div onClick={()=>goToPage(`${tag}/${id}`)} className={Styles.filmCard}>
        <span className={Styles.filmCard__year}>{year}</span>
        <span className={Styles.filmCard__rate}>{vote_average}</span>
        <img className={Styles.filmCard__img} src={imagesSource + poster_path} alt='' />
        <div className={Styles.filmCard__footer}>
          <h2 className={Styles.filmCard__title}>{title || name}</h2>
        </div>
      </div>
    )
};
