// Core
import React from 'react';
// Styles
import Styles from './styles/index.module.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faStar as faStarRegular, faStarHalf as faStarRegularHalf } from '@fortawesome/free-regular-svg-icons';

export const Rating = ({value}) => {

    const valueInteger = value | 0;

    return (
      <div className={Styles.rating}>
        <div className={Styles.rating__section1}>
        {[...Array(10)].map((x, i) =>
            <FontAwesomeIcon icon={faStarRegular} key={i} className={Styles.rating__empty} size="lg" />
        )}
        </div>
        <div className={Styles.rating__section2}>
        {[...Array(valueInteger)].map((x, i) =>
          <FontAwesomeIcon icon={faStarRegular} key={i} className={Styles.rating__color} size="lg" />

        )}
        {(value % 1 !== 0) && <FontAwesomeIcon icon={faStarRegularHalf} className={Styles.rating__color} size="lg" />}
        </div>
      </div>
    )
};
