import {types} from './types';

const initialState = {
    data: [],
    isFetching: false,
    error: false,
};

export const actorsReducer = (state = initialState, {type, payload}) => {
    switch (type) {
        case types.ACTORS_FILL:
            return {...state, data: payload};
        case types.ACTORS_START_FETCHING:
            return {...state, isFetching: true};
        case types.ACTORS_STOP_FETCHING:
            return {...state, isFetching: false};
        case types.ACTORS_SET_FETCHING_ERROR:
            return {...state, error: payload};

        default:
            return state;
    }
};
