import React from 'react';
import { imagesSource } from '../../api/config';
import { useFilmFetch } from './hooks/useFilmFetch';
import { useParams } from 'react-router-dom';
// Layouts
import { Base } from '../../layouts/base';
// Utils
import Styles from './styles/index.module.scss';
import { Rating } from '../../elements/Rating';
import { Actors } from '../Actors';
import { Genres } from '../../elements/Genres';
import { Spinner } from '../../elements/Spinner';

export const Film = () => {
	const { id } = useParams();
	const tag = 'movie';
	const { isFetching, data, error } = useFilmFetch(id);

	const errorAuth = error?.status === 401 && <p>{error.message}</p>;
	const errorMessage = error?.status === 404 && <p>Not found!</p>;

	const loader = isFetching && <Spinner isFetching={isFetching} />;
	const {
		title,
		original_title,
		overview,
		poster_path,
		backdrop_path,
		release_date,
		vote_average,
		genres,
	} = data;
	const image = backdrop_path && imagesSource + backdrop_path;
	const poster = poster_path && imagesSource + poster_path;
	const year = release_date && new Date(release_date).getFullYear();
	const moviesJSX = !isFetching && (data !== null) > 0 && (

		<div className={Styles.film}>
			<h1 className={Styles.film__title}>{title} ({original_title})</h1>
			<div className={Styles.film__info}>
				<div className={Styles.film__poster}>
					{poster_path ? <img src={poster} alt={imagesSource + poster} /> : 'Изображение отсутствует'}
				</div>
				<div className={Styles.film__overview}>
					<p className={Styles.film__caption}>
						{overview}
					</p>
					<div className={Styles.film__image}>
						{backdrop_path && <img src={image} alt={imagesSource + backdrop_path} />}
					</div>
					<Rating value={vote_average} />
					<Actors tag={tag}/>
					<Genres genres={genres} />
					<div className={Styles.film__date}>
						<span className={Styles.film__dateTitle}>Год выхода:</span>
						<ul className={Styles.film__dateNum}>
							{year}
						</ul>
					</div>
				</div>
			</div>
		</div>

	);

	return (
		<Base>
			{errorAuth}
			{errorMessage}
			{loader}
			{moviesJSX}
		</Base>
	);
};
