// Core
import { combineReducers } from 'redux';

// Reducers
import { popularFilmsReducer as popularFilms } from '../components/PopularFilms/reducer';
import { actorCreditsReducer as actorCredits } from '../components/ActorCredits/reducer';
import { filmReducer as film } from '../components/Film/reducer';
import { tvReducer as tv } from '../components/Tv/reducer';
import { popularTvReducer as popularTv } from '../components/PopularTv/reducer';
import { peopleReducer as people } from '../components/People/reducer';
import { peoplePostReducer as peoplePost } from '../components/PeoplePost/reducer';
import { actorsReducer as actors } from '../components/Actors/reducer';

export const rootReducer = combineReducers({
	popularFilms,
	film,
	tv,
	popularTv,
	actors,
	people,
	peoplePost,
	actorCredits
});
