// Types
import { types } from './types';
// Instruments
import { api } from './api';

export const actorsActions = Object.freeze({
	// Sync
	startFetching: () => {
		return {
			type: types.ACTORS_START_FETCHING,
		};
	},
	stopFetching: () => {
		return {
			type: types.ACTORS_STOP_FETCHING,
		};
	},
	setFetchingError: (error) => {
		return {
			type: types.ACTORS_SET_FETCHING_ERROR,
			error: true,
			payload: error,
		};
	},
	fill: (payload) => {
		return {
			type: types.ACTORS_FILL,
			payload,
		};
	},
	// Async
	fetchAsync: (tag,id) => async (dispatch) => {
		dispatch({
			type: types.ACTORS_FETCH_ASYNC,
		});
		dispatch(actorsActions.startFetching());
		try {
			const response = await api.getActors.fetch(tag,id);
			if (response.status === 200) {
				const { data } = response;
				dispatch(actorsActions.fill(data));
			} else {
				const error = {
					status: response.status,
				};
				dispatch(actorsActions.setFetchingError(error));
			}
		} catch (error) {
			dispatch(actorsActions.setFetchingError({
				status: 401,
				message: 'Not authorized. Provide correct token please',
			}));
		}
		dispatch(actorsActions.stopFetching());
	},
});
