// Core
import React from 'react';
// Hooks
import { useActorCredits } from './hooks/useActorCredits';
import { useParams } from 'react-router-dom';
// Utils
import { FilmCard } from '../FilmCard';
import { Spinner } from '../../elements/Spinner';

export const ActorCredits = () => {

	const { id } = useParams();

	const { isFetching, data, error } = useActorCredits(id);

	const errorAuth = error?.status === 401 && <p>{error.message}</p>;
	const errorMessage = error?.status === 404 && <p>Not found!</p>;

	const loader = isFetching && <Spinner isFetching={isFetching} />;

	const dataResults = data.cast;
	const tag = 'films';


	const films = dataResults?.map((film, i) => {
		return <FilmCard {...film} tag={tag}  key={film.id + i} />;
	});

	return (
      <>
				{errorAuth}
				{errorMessage}
				{loader}
				{films}
			</>
	);
};
