import React from 'react';
import { imagesSource } from '../../api/config';
import { usePeoplePost } from './hooks/usePeoplePost';
import { useParams } from 'react-router-dom';
// Layouts
import { Base } from '../../layouts/base';
// Utils
import Styles from './styles/index.module.scss';
import { ActorCredits } from '../ActorCredits';

export const PeoplePost = () => {
	const { id } = useParams();
	const { isFetching, data, error } = usePeoplePost(id);

	const errorAuth = error?.status === 401 && <p>{error.message}</p>;
	const errorMessage = error?.status === 404 && <p>Not found!</p>;

	const loader = isFetching && <div>Страница загружается ...</div>;
	const {
		name,
		biography,
		profile_path,
		birthday,
		place_of_birth,
	} = data;
	const poster = profile_path && imagesSource + profile_path;
	const year = birthday && new Date(birthday).getFullYear();
	const moviesJSX = !isFetching && (data !== null) > 0 && (

		<div className={Styles.peoplePost}>
			<h1 className={Styles.peoplePost__title}>{name}</h1>
			<div className={Styles.peoplePost__info}>
				<div className={Styles.peoplePost__poster}>
					{profile_path && <img src={poster} alt={imagesSource + poster} />}
				</div>
				<div className={Styles.peoplePost__overview}>
					<p className={Styles.peoplePost__caption}>
						{biography}
					</p>
					<div className={Styles.peoplePost__date}>
						<span className={Styles.peoplePost__dateTitle}>Место рождения:</span>
						<ul className={Styles.peoplePost__dateNum}>
							{place_of_birth}
						</ul>
					</div>
					<div className={Styles.peoplePost__date}>
						<span className={Styles.peoplePost__dateTitle}>Дата рождения:</span>
						<ul className={Styles.peoplePost__dateNum}>
							<li>{year}</li>
						</ul>
					</div>
					<div className={Styles.peoplePost__creditsWrap}>
						<div className={Styles.peoplePost__credits}>
							<ActorCredits />
						</div>
					</div>
				</div>
			</div>
		</div>

	);

	return (
		<Base>
			{errorAuth}
			{errorMessage}
			{loader}
			{moviesJSX}
		</Base>
	);
};
