import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { actorCreditsActions } from '../actions';

export const useActorCredits = (id) => {
	const dispatch = useDispatch();

	useEffect(() => {
		dispatch(actorCreditsActions.fetchAsync(id));
	}, [dispatch, id]);

	const { isFetching, error, data } = useSelector((state) => state.actorCredits);

	return {
		data,
		isFetching,
		error,
	};
};
