// Core
import React from 'react';
// Hooks
import { usePopularFilms } from './hooks/usePopularFilms';
// Utils
import { FilmCard } from '../FilmCard';
import { Spinner } from '../../elements/Spinner';

export const PopularFilms = () => {

	const { isFetching, data, error } = usePopularFilms();

	const errorAuth = error?.status === 401 && <p>{error.message}</p>;
	const errorMessage = error?.status === 404 && <p>Not found!</p>;
	const loader = isFetching && <Spinner isFetching={isFetching} />;

	const dataResults = data.results;
	const tag = 'films';



	const films = dataResults?.map((film) => {
		return <FilmCard {...film} tag={tag}  key={film.id} />;
	});

	return (

      <>
				{errorAuth}
				{errorMessage}
				{loader}
				{films}
			</>
	);
};
