import axios from 'axios';

import {token, uri} from '../../../api/config';

export const api = Object.freeze({
    getTv: {
        fetch: (id) => {
            return axios.get(`${uri}/tv/${id}`, {
                params: {
                    api_key: token,
                    language: 'ru-Ru'
                }
            })
        }
    },
});
