import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { popularTvActions } from '../actions';

export const usePopularTv = () => {
	const dispatch = useDispatch();

	useEffect(() => {
		dispatch(popularTvActions.fetchAsync());
	}, [dispatch]);

	const { isFetching, error, data } = useSelector((state) => state.popularTv);

	return {
		data,
		isFetching,
		error,
	};
};
