// Types
import {types} from './types';
// Instruments
import {api} from './api';

export const peoplePostActions = Object.freeze({
    // Sync
    startFetching: () => {
        return {
            type: types.PEOPLEPOST_START_FETCHING
        }
    },
    stopFetching: () => {
        return {
            type: types.PEOPLEPOST_STOP_FETCHING,
        }
    },
    setFetchingError: (error) => {
        return {
            type: types.PEOPLEPOST_SET_FETCHING_ERROR,
            error: true,
            payload: error,
        }
    },
    fill: (payload) => {
        return {
            type: types.PEOPLEPOST_FILL,
            payload
        }
    },
    // Async
    fetchAsync: (id) => async (dispatch) => {
        dispatch({
            type: types.PEOPLEPOST_FETCH_ASYNC
        });
        dispatch(peoplePostActions.startFetching());
        try {
            const response = await api.getPeoplePost.fetch(id);
            if (response.status === 200) {
                const {data} = response;
                dispatch(peoplePostActions.fill(data));
            } else {
                const error = {
                    status: response.status
                };
                dispatch(peoplePostActions.setFetchingError(error));
            }
        } catch (error) {
            dispatch(peoplePostActions.setFetchingError({
                status: 401,
                message: 'Not authorized. Provide correct token please',
            }));
        }
        dispatch(peoplePostActions.stopFetching());
    }
});
