import axios from 'axios';

import {token, uri} from '../../../api/config';

export const api = Object.freeze({
    getPeople: {
        fetch: (page=1) => {
            return axios.get(`${uri}/person/popular?page=${page}`, {
                params: {
                    api_key: token,
                    language: 'ru-Ru'
                }
            })
        }
    },
});
