// Core
import React from 'react';
// Hooks
import { usePopularTv } from './hooks/usePopularTv';
// Utils
import { FilmCard } from '../FilmCard';
import { Spinner } from '../../elements/Spinner';

export const PopularTv = () => {

	const { isFetching, data, error } = usePopularTv();

	const errorAuth = error?.status === 401 && <p>{error.message}</p>;
	const errorMessage = error?.status === 404 && <p>Not found!</p>;
	const loader = isFetching && <Spinner isFetching={isFetching} />;

	const dataResults = data.results;
	const tag = 'tv';


	const tvs = dataResults?.map((tv) => {
		return <FilmCard {...tv} tag={tag}  key={tv.id} />;
	});

	return (

      <>
				{errorAuth}
				{errorMessage}
				{loader}
				{tvs}
			</>

	);
};
