import {useEffect} from 'react';
import {useDispatch, useSelector} from 'react-redux';

import {tvActions} from '../actions';

export const useTvFetch = (id) => {
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(tvActions.fetchAsync(id));
    }, [dispatch, id]);

    const {isFetching, error, data} = useSelector((state) => state.tv);

    return {
        data,
        isFetching,
        error
    }
};
