import {useEffect} from 'react';
import {useDispatch, useSelector} from 'react-redux';

import {actorsActions} from '../actions';

export const useActorsFetch = (tag,id) => {
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(actorsActions.fetchAsync(tag,id));
    }, [dispatch, tag, id]);

    const {isFetching, error, data} = useSelector((state) => state.actors);

    return {
        data,
        isFetching,
        error
    }
};
