// Core
import React from 'react';
// Hooks
import { usePeople } from './hooks/usePeople';
// Utils
import { PeopleCard } from '../PeopleCard';
import { Base } from '../../layouts/base';
import { Spinner } from '../../elements/Spinner';

export const People = () => {

	const { isFetching, data, error } = usePeople();

	const errorAuth = error?.status === 401 && <p>{error.message}</p>;
	const errorMessage = error?.status === 404 && <p>Not found!</p>;
	const loader = isFetching && <Spinner isFetching={isFetching} />;

	const dataResults = data.results;
	const tag = 'films';


	const people = dataResults?.map((people,i) => {
		return <PeopleCard {...people} tag={tag}  key={people.id + i} />;
	});

	return (

		<Base>
			{errorAuth}
			{errorMessage}
			{loader}
			{people}
		</Base>
	);
};
