// Core
import React from 'react';

// Styles
import Styles from './styles/index.module.scss';

// Elements

export const Base = ({ children }) => {



	return (
		<section className={Styles.base}>

			<div className={Styles.base__wrapper}>
				<div className={Styles.base__container}>
					{children}
				</div>

			</div>
		</section>
	);
};
