// Core
import React from 'react';
// Styles
import Styles from './styles/index.module.scss';
import { imagesSource } from '../../api/config';
// Elements
import { goToPage } from '../../helpers/goToPage';

export const PeopleCard = ({ profile_path, title, name, id, tag, release_date, vote_average }) => {

    return (
      <div onClick={()=>goToPage(`people/${id}`)} className={Styles.peopleCard}>
        <img className={Styles.peopleCard__img} src={imagesSource + profile_path} alt='' />
        <div className={Styles.peopleCard__footer}>
          <h2 className={Styles.peopleCard__title}>{title || name}</h2>
        </div>
      </div>
    )
};
