// Types
import {types} from './types';
// Instruments
import {api} from './api';

export const popularFilmsActions = Object.freeze({
    // Sync
    startFetching: () => {
        return {
            type: types.POPULARFILMS_START_FETCHING
        }
    },
    stopFetching: () => {
        return {
            type: types.POPULARFILMS_STOP_FETCHING,
        }
    },
    setFetchingError: (error) => {
        return {
            type: types.POPULARFILMS_SET_FETCHING_ERROR,
            error: true,
            payload: error,
        }
    },
    fill: (payload) => {
        return {
            type: types.POPULARFILMS_FILL,
            payload
        }
    },
    // Async
    fetchAsync: (page) => async (dispatch) => {
        dispatch({
            type: types.POPULARFILMS_FETCH_ASYNC
        });
        dispatch(popularFilmsActions.startFetching());
        try {
            const response = await api.getPopularMovies.fetch(page);
            if (response.status === 200) {
                const {data} = response;
                dispatch(popularFilmsActions.fill(data));
            } else {
                const error = {
                    status: response.status
                };
                dispatch(popularFilmsActions.setFetchingError(error));
            }
        } catch (error) {
            dispatch(popularFilmsActions.setFetchingError({
                status: 401,
                message: 'Not authorized. Provide correct token please',
            }));
        }
        dispatch(popularFilmsActions.stopFetching());
    }
});
