// Pages
import { Home } from '../pages/home';
import { Films } from '../pages/films';
import { People } from '../pages/people';
import { PeoplePost } from '../pages/peoplePost';
import { Film } from '../pages/film';
import { Tv } from '../pages/tv';
import { Tvs } from '../pages/tvs';



export const base = '/movies-react-dist/';

export const book = Object.freeze({
    root: {
        url: `${base}`,
        page: Home,
        title: 'Главная',
        menu: true,
    },
    filmPage: {
        url: `${base}films/:id`,
        page: Film,
        title: 'СТраница фильма',
        menu: false,
    },
    tvPage: {
        url: `${base}tv/:id`,
        page: Tv,
        title: 'СТраница сериала',
        menu: false,
    },
    films: {
        url: `${base}films`,
        page: Films,
        title: 'Фильмы',
        menu: true,
    },
    tv: {
        url: `${base}tv`,
        page: Tvs,
        title: 'Сериалы',
        menu: true,
    },
    people: {
        url: `${base}people`,
        page: People,
        title: 'Люди',
        menu: true,
    },
    peoplePage: {
        url: `${base}people/:id`,
        page: PeoplePost,
        title: 'Страница человека',
        menu: false,
    },
});
