// Types
import {types} from './types';
// Instruments
import {api} from './api';

export const popularTvActions = Object.freeze({
    // Sync
    startFetching: () => {
        return {
            type: types.POPULARTV_START_FETCHING
        }
    },
    stopFetching: () => {
        return {
            type: types.POPULARTV_STOP_FETCHING,
        }
    },
    setFetchingError: (error) => {
        return {
            type: types.POPULARTV_SET_FETCHING_ERROR,
            error: true,
            payload: error,
        }
    },
    fill: (payload) => {
        return {
            type: types.POPULARTV_FILL,
            payload
        }
    },
    // Async
    fetchAsync: () => async (dispatch) => {
        dispatch({
            type: types.POPULARTV_FETCH_ASYNC
        });
        dispatch(popularTvActions.startFetching());
        try {
            const response = await api.getPopularTv.fetch();
            if (response.status === 200) {
                const {data} = response;
                dispatch(popularTvActions.fill(data));
            } else {
                const error = {
                    status: response.status
                };
                dispatch(popularTvActions.setFetchingError(error));
            }
        } catch (error) {
            dispatch(popularTvActions.setFetchingError({
                status: 401,
                message: 'Not authorized. Provide correct token please',
            }));
        }
        dispatch(popularTvActions.stopFetching());
    }
});
