import axios from 'axios';

import {token, uri} from '../../../api/config';

export const api = Object.freeze({
    getPopularTv: {
        fetch: () => {
            return axios.get(`${uri}/tv/popular`, {
                params: {
                    api_key: token,
                    language: 'ru-Ru'
                }
            })
        }
    },
});
