import React from 'react';
import {useActorsFetch} from './hooks/useActorsFetch';
import { useParams } from 'react-router-dom';
// Utils
import Styles from './styles/index.module.scss';
import { goToPage } from '../../helpers/goToPage';

export const Actors = ({tag}) => {
    const {id} = useParams();
    const {isFetching, data, error} = useActorsFetch(tag,id);
    const errorAuth = error.status === 401 && (<p>{error.message}</p>);
    const errorMessage = error.status === 404 && (
        <p>Not found!</p>
    );

    const loader = isFetching && <div>траница загружается</div>;

    const {cast} = data;

    const actors = cast?.map(({name, id},i) => i < 6 && <li key={id}><div onClick={()=>goToPage(`people/${id}`)}>{name}</div></li>);

    const actorsJSX = !isFetching && cast && cast.length > 0 && (
        <div className={Styles.actors}>
            <span className={Styles.actors__title}>Актеры:</span>
            <ul className={Styles.actors__list}>
                {actors}
            </ul>
        </div>
    );

    return (
        <>
            {errorAuth}
            {errorMessage}
            {loader}
            {actorsJSX}
        </>
    );
};
