import React from 'react';
import { imagesSource } from '../../api/config';
import { useTvFetch } from './hooks/useTvFetch';
import { useParams } from 'react-router-dom';
// Layouts
import { Base } from '../../layouts/base';
// Utils
import Styles from './styles/index.module.scss';
import { Rating } from '../../elements/Rating';
import { Actors } from '../Actors';
import { Genres } from '../../elements/Genres';
import {Spinner} from '../../elements/Spinner';

export const Tv = () => {
	const tag = 'tv';
	const { id } = useParams();
	const { isFetching, data, error } = useTvFetch(id);

	const errorAuth = error?.status === 401 && <p>{error.message}</p>;
	const errorMessage = error?.status === 404 && <p>Not found!</p>;

	const loader = isFetching && (
		<Spinner isFetching={isFetching} />
	);
	const {
		name,
		original_name,
		overview,
		poster_path,
		backdrop_path,
		first_air_date,
		vote_average,
		genres,
	} = data;

	const image = backdrop_path && imagesSource + backdrop_path;
	const poster = poster_path && imagesSource + poster_path;
	const year = first_air_date && new Date(first_air_date).getFullYear();

	const tvsJSX = !isFetching && (data !== null) > 0 && (

		<div className={Styles.tv}>
			<h1 className={Styles.tv__title}>{name} ({original_name})</h1>
			<div className={Styles.tv__info}>
				<div className={Styles.tv__poster}>
					<img src={poster} alt={imagesSource + poster} />
				</div>
				<div className={Styles.tv__overview}>
					<p className={Styles.tv__caption}>
						{overview}
					</p>
					<div className={Styles.tv__image}>
						<img src={image} alt={imagesSource + backdrop_path} />
					</div>
					<Rating value={vote_average} />
					<Actors tag={tag}/>
					<Genres genres={genres}/>
					<div className={Styles.tv__date}>
						<span className={Styles.tv__dateTitle}>Год выхода:</span>
						<ul className={Styles.tv__dateNum}>
							{year}
						</ul>
					</div>
				</div>
			</div>
		</div>

	);

	return (
		<Base>
			{errorAuth}
			{errorMessage}
			{loader}
			{tvsJSX}
		</Base>
	);
};
