import {useEffect} from 'react';
import {useDispatch, useSelector} from 'react-redux';

import {peoplePostActions} from '../actions';

export const usePeoplePost = (id) => {
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(peoplePostActions.fetchAsync(id));
    }, [dispatch, id]);

    const {isFetching, error, data} = useSelector((state) => state.peoplePost);

    return {
        data,
        isFetching,
        error
    }
};
